die {
    text-align: center;
}

.die-container {
    position: relative;
    width: 225px;
    height: 225px;
    margin: auto;
    cursor: pointer;
}

/*
    Common Die Style
*/
#die {
    position: relative;
    width: 100%;
    height: 100%;
    margin: auto;
    color: #FFFFFF;
    filter: "progid:DXImageTransform.Microsoft.Dropshadow(OffX= 0, OffY=5, Color='#444')";
    filter: url(#drop-shadow);
    -webkit-filter: drop-shadow(0px 5px 3px rgba(0, 0, 0, 0.5));
    filter: drop-shadow(0px 5px 3px rgba(0, 0, 0, 0.5));
}

#die:active {
}

/*
    Die Styles
*/
.d4 {
    background-image: url(./images/d4.svg);
}

.d6 {
    background-image: url(./images/d6.svg);
}

.d8 {
    background-image: url(./images/d8.svg);
}

.d10 {
    background-image: url(./images/d10.svg);
}

.d12 {
    background-image: url(./images/d12.svg);
}

forge {
    display: flex;
    flex-direction: row;
    justify-content: center;
    text-align: center;
    margin: 30px auto;
}

#deforge {
    margin: auto 12px;
    width: 100px;
    height: 100px;
    border-radius: 10px;
    background-color: #c14343;
    border: 1.5px solid #333;
    background-image: url(./images/deforge.svg);
    cursor: pointer;
    -webkit-box-shadow: inset 0px 0px 17px -2px rgba(0, 0, 0, 0.4);
    -moz-box-shadow: inset 0px 0px 17px -2px rgba(0, 0, 0, 0.4);
    box-shadow: inset 0px 0px 17px -2px rgba(0, 0, 0, 0.4);
    filter: "progid:DXImageTransform.Microsoft.Dropshadow(OffX= 0, OffY=5, Color='#444')";
    filter: url(#drop-shadow);
    -webkit-filter: drop-shadow(0px 5px 3px rgba(0, 0, 0, 0.3));
    filter: drop-shadow(0px 5px 3px rgba(0, 0, 0, 0.3));
}

#deforge:active {
    background-color: #a93a3a;
    -webkit-box-shadow: inset 0px 0px 17px 2px rgba(0, 0, 0, 0.5);
    -moz-box-shadow: inset 0px 0px 17px 2px rgba(0, 0, 0, 0.5);
    box-shadow: inset 0px 0px 17px 2px rgba(0, 0, 0, 0.5);
}

